<template>
    <div class="wrapper">
        <image-thumb class="thumb" v-for="item in data" :image="item"/>
    </div>
</template>

<script>
    import ImageThumb from '../components/ImageThumb.vue'

    const STORAGE_KEY = 'ars-data';

    export default {
        components: {ImageThumb},
        data() {
            let data = localStorage.getItem(STORAGE_KEY);
            data = data && JSON.parse(data) || [];

            data.forEach(item => {
                if (item.ry < 0) {
                    item.flip = true;
                }
            });

            return {data};
        }
    };
</script>

<style lang="scss" scoped>
    .wrapper {
        margin: 15px;

        .thumb {
            vertical-align: top;
            margin: 5px;
        }
    }
</style>